                let name = item['name'];
                if (name && texture) {

                    item['vars'] = [];

                    for (let a in this.prod[texture][color1]) {
                        if (name in this.prod[texture][color1][a]) {
                            console.log(a)

                            console.log(this.prod[texture][color1][a][name])
                            item['vars'].push(a);

                        }

                    }

                    return item['vars']
                } else {
                    return false
                }


            },
            itIsZinc: function (item, texture) {

                if (texture === 'zinc') {
                    item['vars'] = [];

                    console.log('it is zinc')
                    let name = item['name'];
                    for (let a in this.prod['zinc']) {
                        if (name in this.prod['zinc'][a]) {
                            console.log(a)

                            console.log(this.prod['zinc'][a][name])
                            item['vars'].push(a);
                        }

                    }
                } else {
                    console.log('no zink')
                }


            },
            howMany: function (a, b, q) {
                if (isNaN(a)) {
                    a = 0;
                }
                if (isNaN(b)) {
                    b = 0;
                }
                if (q === undefined) {
                    q = 1;
                }
                let n = a * b * q;
                n = n.toFixed(2);
                n = parseFloat(n);
                if (n !== '0.00') {
                    return n
                }

            },
            formatToFixsed2: function (item) {
                item.length = parseFloat(item.length.toFixed(2))

            },
            formatToFixsed: function (item) {
                item.count = parseFloat(item.count.toFixed(0))

            },
            str_r: function (in_) {
                //UKR_0.45
                in_ = in_.split('_');
                switch (in_[0].toUpperCase()) {
                    case 'UKR':
                        in_[0] = 'Украина';
                        break;
                    case 'CHN':
                        in_[0] = 'Китай';
                        break;
                    case 'ITA':
                        in_[0] = 'Италия';
                        break;
                    case 'POL':
                        in_[0] = 'Польша';
                        break;
                    case 'DEU':
                        in_[0] = 'Германия';
                        break;
                    default:
                        return;
                }
                in_[1] = in_[1].replace(/\./, ",")

                console.log(in_[1])
                return in_.join(' ') + ' мм';

            },
            checkForm: function (item, index) {
                let textureToCart = '';
                let texture = item['texture'];
                let color = item['color'];
                let metall = item['metall'];
                let name = item['name'];
                let title = item['title'];
                console.log(name)
                if (name === 'samorezy') {

                    if (item['count'] && color) {
                        item['errors'] = []
                        let samorezyColor = color.toUpperCase()
                        let samorezySum = this.howMany(item['itemCost'], item['count'])
                        color = '';
                        this.showGoodMsg(index, item);
                        this.cart.push({
                            name: title,
                            count: item['count'],
                            description: samorezyColor,
                            sum: samorezySum,
//                            price: item['price'],
                            itemCost: item['itemCost']

                        });
                        item['count'] = 0
                        return true
                    } else {

                        item['errors'] = [];
                        if (!item['count'] > 0) item.errors.push("Укажите количество");
                        if (!color) item.errors.push("Укажите цвет");
                        return false
                    }
                }


                if (item['length'] && item['count'] > 0 && item['texture'] && item['metall']) {
                    this.showGoodMsg(index, item);
                    if (texture === 'zinc') {
                        textureToCart = 'Оцинковка';
                        item['price'] = this.prod[texture][metall][name]
                    } else {
                        if (texture === 'matted') {
                            textureToCart = 'Матовый полиэстер';
                        } else {
                            textureToCart = 'Глянцевый полиэстер';
                        }
                        item['price'] = this.prod[texture][color][metall][name]
                    }
                    let sum = this.howMany(item['price'], item['quantity']);
                    let itemCost = this.howMany(item['price'], item['width'], item['length']);

                    let description = color.toUpperCase() + ' ' + textureToCart + ' ' + this.str_r(metall)
                    let itemOfCart = {
                        articul: name,
                        name: title,
                        description: description,
                        length: item['length'],
                        count: item['count'],
                        price: item['price'],
                        width: item['width'],
                        quantity: item['quantity'],
                        sum: sum,
                        itemCost: itemCost
                    };
                    this.cart.push(itemOfCart);

                    console.log(item['price']);
                    item['errors'] = [];
                    item['count'] = 0;
                    item['quantity'] = '';
                    item['texture'] = '';
                    item['color'] = '';
                    item['metall'] = '';
                    item['price'] = '';
                    return true;
                }

                item['errors'] = [];
                if (!this.lengthCorrect) item.errors.push("Длина листа должна быть кратна 0.05 см");
                if (!item['count'] > 0) item.errors.push("Укажите количество листов");
                if (!item['metall']) item.errors.push("Укажите все опции товара");

//                if(!this.age) this.errors.push("Age required.");
            },

            iseEptyArray: function (a) {
                if (typeof a !== 'undefined' && a.length > 0) {
                    return false
                } else {
                    return true
                }
            }

        },
        created() {
            console.log(this.prod)

        },
        mounted() {
            document.querySelector('#samorzy0 input').readOnly = true
            document.querySelector('#samorzy1 input').readOnly = true
        }

    }
</script>

