<script>


    export default {
        name: 'DeliveryInfo',
        props: {
            userInfo: Object,
            deliveryPrice: Object,
            checkUserInfo: Object,
        },
        component: {},
        data() {
            return {
                roadMap: [
                    {region: 'Полтавский', km: 20},
                    {region: 'Диканьский', km: 30},
                    {region: 'Машевский', km: 40},
                    {region: 'Новосанжарский', km: 40},
                    {region: 'Решетиловский', km: 40},
                    {region: 'Чутовский', km: 55},
                    {region: 'Карловский', km: 60},
                    {region: 'Котелевский', km: 65},
                    {region: 'Шишацкий', km: 70},
                    {region: 'Кобелякский', km: 75},
                    {region: 'Великобагачанский', km: 80},
                    {region: 'Зеньковский', km: 80},
                    {region: 'Козельщинский', km: 90},
                    {region: 'Миргородский', km: 110},
                    {region: 'Хорольский', km: 110},
                    {region: 'Гадячский', km: 115},
                    {region: 'Кременчугский', km: 120},
                    {region: 'Семёновский', km: 130},
                    {region: 'Глобинский', km: 140},
                    {region: 'Лубенский', km: 145},
                    {region: 'Оржицкий', km: 160},
                    {region: 'Гребёнковский', km: 190},
                    {region: 'Лохвицкий', km: 190},
                    {region: 'Пирятинский', km: 190},
                    {region: 'Чернухинский', km: 200},
                ],


                region: '',
                goodSubmit: '',
                formErrors: [],
                formErrors2: []
            };
        },
        methods: {
            changed: function () {
                let rez = this.roadMap.find(x => x.region === this.region);
                if (typeof rez != 'undefined') {
                    console.log(rez)
                    this.userInfo.km = rez.km
                } else {
                    this.userInfo.km = null
                }
            },
            console: function () {
                console.log(1)
            },
            deliverySubmit: function (e) {

                this.formErrors = [];
                this.formErrors2 = [];
                let a = this.userInfo;
                let phone = /^(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){10,14}(\s*)?$/.test(a.phone);
                let km = /^[0-9]{1,4}$/.test(a.km);


                if (a.name && a.last && phone && km && a.address) {
                    a.checked ? this.goodSubmit = 'Ваши данные обновлены' : this.goodSubmit = 'Ваши данные получены'
                    a.checked = true
                    this.deliveryObject(a.km)
                    this.checkUserInfo.first = a.name;
                    this.checkUserInfo.last = a.last;
                    this.checkUserInfo.phone = a.phone;
                    this.checkUserInfo.region = this.region;
                    a.region = this.region;
                    this.checkUserInfo.city = a.city;
                    this.checkUserInfo.address = a.address


                    console.log(a.checked)
                    return true
                } else {

                    if (a.name || a.last || a.phone) {
                        if (!a.name) this.formErrors.push("Укажите ваше имя");
                        if (!a.last) this.formErrors.push("Укажите вашу фамилию");
                        if (!a.phone) {
                            this.formErrors.push("Укажите ваш телефон")
                        } else {
                            if (!phone) this.formErrors.push("Телефон указан не в верном формате ");
                        }
                    } else {
                        this.formErrors.push("Укажите данные о себе");
                    }

                    if (this.region || a.address) {
                        if (!this.region) {
                            this.formErrors2.push("Укажите ваш регион")
                        } else {
                            if (!km) this.formErrors2.push("Укажите регион из выпадающего списка/подсказки");
                        }
                        if (!a.address) this.formErrors2.push("Укажите ваш адресс");
                        if (!a.city) this.formErrors2.push("Укажите ваш город");
                    } else {
                        this.formErrors2.push("Укажите данные о доставке");
                    }
                    console.log(this.formErrors)
                    e.preventDefault()
                }
            },
            iseEptyArray: function (a) {
                if (typeof a !== 'undefined' && a.length > 0) {
                    return false
                } else {
                    return true
                }
            },
            deliveryObject: function (km) {
                this.userInfo.price = {
                    49: this.deliveryPrice[km]['to50'],
                    50: this.deliveryPrice[km]['from50'],
                    100: this.deliveryPrice[km]['from100'],
                    150: this.deliveryPrice[km]['from150']
                }


            }

        },
        computed: {},
        watch: {},
        created() {
            console.log(this.$refs.regionInput)


        }


    }
</script>

