<script>
import axios from 'axios'



    export default {
        name: 'SiteHeader',
        props: {
            userInfo: Object,
            cart: Array,
            checkUserInfo: Object
        },
        data() {
            return {
                errors: [],
                total: null,
		            priceOfDelivery: 0,
		            to15: 0,
		            to5: 0,
		            redyToCheck: false,
		            redyDelivery: false
            };
        },
        methods: {
            abotDeliverePrice(){
                if(!this.userInfo.checked){
                    return 'Введите данные о доставке'
                }else {
                    if (this.priceOfDelivery === 0){
                        return 'Бесплатно'
                    }else{
                        return this.priceOfDelivery
                    }
                }
            },

            iseEptyArray: function (a) {
                if (typeof a !== 'undefined' && a.length > 0) {
                    return false
                } else {
                    return true
                }
            },
            checkOut(){
                let mch = 15 - this.to15;
                let prof = 5 - this.to5;
                mch.toFixed(2)
                prof.toFixed(2)


                this.errors = [];
                if(this.to5 === 0 && this.to15 === 0) {
                    this.errors.push("Чтобы сделать заказ, добавьте профнастил или металлочерепицу в корзину")
		            }else{

                    if(this.to5 && this.to5 < 5 ){
                        this.errors.push("Минимальная заказ профиля - 5м², для оплаты добавьте еще " + prof.toFixed(2) + "м²");
                    }else {

                    }

                    if(this.to15 && this.to15 < 15 ){
                        this.errors.push("Минимальная заказ металлочерепицы - 15м² для оплаты добавьте еще " + mch.toFixed(2) + "м²" )
                    }


		            }


//
//                if (!(this.to5 === 0 && this.to15 >= 15)){
//                    this.errors.push("Минимальная длинна металочерипицы 15м²")
//                } else{
//                    if (!(this.to15 === 0 && this.to5 >= 5 )){
//                        this.errors.push("Минимальная длинна профиля 5м²");
//                    }else {
//                        if (!(this.to15 > 0 && this.to5 > 0 )) this.errors.push("Минимальная длинна профиля 5м² и металочерипицы 15м²");
//                    }
//                }




                console.log(this.to5 === 0 && this.to15 >= 15)
                console.log(this.to15 === 0 && this.to5 >= 5 )
                console.log(this.to15 >= 15 && this.to5 >= 5 )

                if((this.to5 === 0 && this.to15 >= 15) || (this.to15 === 0 && this.to5 >= 5 ) || (this.to15 >= 15 && this.to5 >= 5 )){


                    if(this.checkUserInfo.region && this.checkUserInfo.phone){
                        let cartForSend = this.cart
                        let deliveryInfoSend = JSON.stringify( this.checkUserInfo, null, 2);
                        deliveryInfoSend = deliveryInfoSend.split('"').join('');
                        console.log(deliveryInfoSend)


                        for(let i = 0; i < cartForSend.length; i++){
                            delete cartForSend[i].width;
                            delete cartForSend[i].articul;
                        }
                        cartForSend = JSON.stringify( cartForSend, null, 2);
                        cartForSend = cartForSend.split('"').join('');
                        cartForSend = cartForSend.split('length').join('Длинна');
                        cartForSend = cartForSend.split('count').join('Кол-во');
                        cartForSend = cartForSend.split('price').join('Цена за м2');
                        cartForSend = cartForSend.split('itemCost').join('Цена за штуку');
                        cartForSend = cartForSend.split('sum').join('Сумма');
                        cartForSend = cartForSend.split('quantity').join('всего м2');



                        const vm = this;
                        let xhr = new XMLHttpRequest();
                        xhr.open("POST", 'receiver/handler.php');
                        xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
                        xhr.onreadystatechange = function(e)
                        {
                            if(xhr.readyState==4)
                            {
                                let ret_obj = JSON.parse(xhr.responseText);
                                console.log(ret_obj);
                                vm.liqpayParms = ret_obj
                                console.log(this.liqpayParms)
                                console.log(this.liqpayParms)

                                let form = document.getElementById('form_for_submit_liqpay');
                                let inpts = form.getElementsByTagName('input');
                                inpts[0].value = ret_obj.data;
                                inpts[1].value = ret_obj.signature;
                                form.submit()




                            }
                        }
                        xhr.send(this.encodeFormData({
                            amount : this.total,
		                        cart: cartForSend,
                            delivery: deliveryInfoSend,
                            deliveryPrice: this.priceOfDelivery + ' грн стоимость доставки'
                        }));





                    }else {
                        this.errors.push("Введите ваши контактные данные")
                        return false
                    }
                }else {
                    return false
                }


            },
            submitFormOnChainge(){
                let form = document.getElementById('form_for_submit_liqpay');
                console.log('все ок')
                form.submit()
            },
            encodeFormData(data) {
                if (!data) return "";
                let pairs = [];
                for (let name in data) {
                    if (!data.hasOwnProperty(name)) continue;

                    if (typeof data[name] === "function") continue;

                    let value = data[name].toString();

                    name = encodeURIComponent(name.replace("%20", "+"));
                    value = encodeURIComponent(value.replace("%20", "+"));

                    pairs.push(name + "=" + value);
                }

                return pairs.join('&');
            },


        },



        created() {
            for(let i = 0; i < this.cart.length; i++ ){
                this.total += this.cart[i]['sum']
                console.log(this.cart[i]['sum'])
                if(this.cart[i]['articul'] === 'mch_monterrey' || this.cart[i]['articul'] === 'mch_trevizo'){
                    this.to15 += this.cart[i]['quantity']
                    console.log(this.cart[i]['name'])
                }
                if(this.cart[i]['articul'] === 'profil' || this.cart[i]['articul'] === 'trapec'){
                    this.to5 += this.cart[i]['quantity']
                    console.log(this.cart[i]['name'])
                }

            }
            if((this.to5 === 0 && this.to15 >= 15) || (this.to15 === 0 && this.to5 >= 5 ) || (this.to15 >= 15 && this.to5 >= 5 )){
                    this.redyToCheck =true

            }else {
                this.redyToCheck =false
            }
        },
        watch:{
            cart: function () {
                this.total = 0
		            this.to5 = 0
                this.to15 = 0
                this.$localStorage.set('lsCart', JSON.stringify(this.cart));
                console.log('add to ls' + this.cart.length)

		            for(let i = 0; i < this.cart.length; i++ ){
                    this.total += this.cart[i]['sum']
				            console.log(this.cart[i]['sum'])
                    if(this.cart[i]['articul'] === 'mch_monterrey' || this.cart[i]['articul'] === 'mch_trevizo'){
                        this.to15 += this.cart[i]['quantity']
		                    console.log(this.cart[i]['name'])
                    }
                    if(this.cart[i]['articul'] === 'profil' || this.cart[i]['articul'] === 'trapec'){
                        this.to5 += this.cart[i]['quantity']
                        console.log(this.cart[i]['name'])
                    }

                }

                    let rez = this.to5 +  this.to15
                     console.log(rez)

                if(rez === 0){
		                console.log(0)
		                this.priceOfDelivery = 0
                }else{
                    if(rez < 50 ){
                        console.log(rez + '=' + this.userInfo['price']['49'])
                        this.priceOfDelivery = this.userInfo['price']['49']

                    }else{
                        if(rez < 100){
                            console.log(rez + '=' + this.userInfo['price']['50'])
                            this.priceOfDelivery = this.userInfo['price']['50']

                        }else{
                            if(rez < 150){
                                console.log(rez + '=' + this.userInfo['price']['100'])
                                this.priceOfDelivery = this.userInfo['price']['100']

                            }else{
                                console.log(rez + '=' + this.userInfo['price']['150'])

                                this.priceOfDelivery = this.userInfo['price']['150']

                            }
                        }
                    }

                }
                console.log(this.total)
                this.total += parseInt(this.priceOfDelivery)
                console.log( parseInt(this.priceOfDelivery))
                console.log(this.total)



            },
            checkUserInfo: {
                handler(){

                    console.log('sdsdsd')
                    if(this.checkUserInfo.region){
                        console.log('rfrfrfr')
                        let rez = this.to5 +  this.to15
                        if(rez === 0){
                            this.priceOfDelivery = 0
                        }else if(rez < 50 ){
                            this.priceOfDelivery = this.userInfo['price']['49']
                        }else if(rez < 100){
                            this.priceOfDelivery = this.userInfo['price']['50']
                        }else if(rez < 150){
                            this.priceOfDelivery = this.userInfo['price']['100']
                        }else {
                            this.priceOfDelivery = this.userInfo['price']['150']
                        }
                        this.total =  parseInt(this.priceOfDelivery)

                        for(let i = 0; i < this.cart.length; i++ ){
                            this.total += this.cart[i]['sum']
                            console.log(this.cart[i]['sum'])
                        }
                    }
                }, deep: true
                },




        },



    }
</script>

