<script>

    export default {
        name: 'SiteHeader',
        props: {
            cart: Array,
            phones: Object
        },
        data() {
            return {

                scrolled: false,
                cartCounter: 0

            };
        },
        methods: {
            handleScroll() {
                this.scrolled = window.scrollY > 0;
                return this.scrolled
            }
        },
        computed: {},
        watch: {
            cart: function () {
                this.cartCounter = this.cart.length
            }

        },
        created() {
            this.cartCounter = this.cart.length
            window.addEventListener('scroll', this.handleScroll);
        },
        destroyed() {
            window.removeEventListener('scroll', this.handleScroll);
        }

    }
</script>

