<template>
	<div id="app">


		<SiteHeader :phones="this.phones" :cart="this.cart"></SiteHeader>


		<main style="" class="l_main">
			<MainCatalog :prod='this.product' :cart="this.cart"></MainCatalog>
			<delivery-info :userInfo="this.userInfo" :deliveryPrice="this.deliveryPrice" :checkUserInfo="this.checkUserInfo"></delivery-info>
			<SiteCart :userInfo="this.userInfo" :cart="this.cart" :checkUserInfo="this.checkUserInfo"></SiteCart>
			<AboutCompany></AboutCompany>
		</main>
		<footer class="l_footer">
			<div class="container" style="display: flex; flex-wrap: wrap">
				<!--<div class="b_phone sm_hide">-->
				<!--<svg class="icon">-->
				<!--<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#phone_icon"></use>-->
				<!--</svg>-->
				<!--<div class="wrapper">-->
				<!--<a v-for="(phone, index) in phones" :key="index" class="item"  :href="'tel:' + phone.replace(/\s/g, '')">{{ phone }}</a>-->
				<!--</div>-->
				<!--</div>-->

			</div>
		</footer>

	</div>
</template>

<script>
    import SiteHeader from './components/SiteHeader.vue'
    import MainCatalog from './components/MainCatalog.vue'
    import DeliveryInfo from './components/DeliveryInfo.vue'
    import SiteCart from './components/SiteCart.vue'
    import AboutCompany from './components/AboutCompany.vue'
    import GetSheetDone from 'get-sheet-done';

    export default {
        name: 'app',
        components: {
            SiteHeader,
            MainCatalog,
            DeliveryInfo,
            SiteCart,
            AboutCompany,
        },

        data() {
            return {
                phones: {
                    1: '+38 099 544 32 72',
                    2: '+38 097 742 26 87'
                },
                checkUserInfo: {
                    first: '',
                    last: '',
                    phone: '',
                    region: '',
                    city: '',
                    address: ''
                },
                errors: [],
                sheetData: null,
                deliveryPrice: null,
                userInfo: {
                    checked: false,
                    first: '',
                    last: '',
                    phone: '',
                    region: '',
                    km: null,
                    price: null,
                    city: '',
                    address: ''
                },
                testNull: '',
                cart: [],
                product: {
                    gloss: {
                        ral3005: {},
                        ral3011: {},
                        ral6005: {},
                        ral6020: {},
                        ral8017: {},
                        ral8019: {},
                        ral1015: {},
                        ral9003: {},
                        ral9006: {}
                    },
                    matted: {
                        ral3005: {},
                        ral3011: {},
                        ral6005: {},
                        ral6020: {},
                        ral8017: {},
                        ral8019: {}
                    },
                    zinc: {}
                }
            };
        },
        methods: {},
        watch: {},
        computed: {},

        mounted() {

        },
        created() {

            let sheetKey = '1tVSMeJkonmjxRQlnt0WIpKyHOO1PBh9hpJE7l5ityR0';                 // KEY OF GOOGLE DOCS
            let timeNow = new Date;
            timeNow = timeNow.getTime();
            let lsSheetData = JSON.parse(this.$localStorage.get('lsSheetData', false));
            let lsDeliveryPrice = JSON.parse(this.$localStorage.get('lsDeliveryPrice', false));
            let catalogLastUpdate = this.$localStorage.get('catalogLastUpdate', false);
            let lsCart = JSON.parse(this.$localStorage.get('lsCart', false));

            if (lsCart) {
                this.cart = lsCart
            }

            function isLastUpdateUpToHour() {     // BOOLEAN RESULT ABOUT LAST UPDATING OF localStorage

                if (catalogLastUpdate) {
                    let differenceTime = timeNow - catalogLastUpdate;
                    let timeToUpdate = 1000 * 60 * 30;                               // 30 min
                    if (differenceTime > timeToUpdate) {
                        console.log('time bitch');

                        return false  // localStorage HAVE OLD DATA
                    } else {
                        console.log('you have time ' + differenceTime + ' From ' + timeToUpdate);
                        return true   // localStorage HAVE FRESH DATA

                    }
                } else {
                    return false  // localStorage DIDN'T HAVE INFORMATION ABOUT LAST UPDATE
                }
            } // END OF isLastUpdateUpToHour()
            console.log(isLastUpdateUpToHour())

            if (lsSheetData && isLastUpdateUpToHour()) {

                console.log(' Axios is not do GET request \n Site use Local Storage ')

                this.product = lsSheetData;
                this.deliveryPrice = lsDeliveryPrice;
                console.log(this.product)

            } else {

                console.log(' (No data in / need Update) local storage \n Starting download data from Google sheets Data Base... ')

                GetSheetDone.labeledCols(sheetKey, 1)
                    .then(sheet => {

                        this.sheetData = sheet.data;
                        this.$localStorage.set('catalogLastUpdate', timeNow);
                        getProduct(this.sheetData, this.product)
                        this.$localStorage.set('lsSheetData', JSON.stringify(this.product));

                        GetSheetDone.labeledColsRows(sheetKey, 2)
                            .then(sheet2 => {

                                this.deliveryPrice = sheet2.data;
                                console.log(this.deliveryPrice)
                                this.$localStorage.set('lsDeliveryPrice', JSON.stringify(this.deliveryPrice));

                            })
                            .catch(e => {
                                console.log(e)
                            });

                    })
                    .catch(e => {
                        console.log(e)
                    });

            }


            function getProduct(obj, prod) {

                for (let i = 0; i < 19; i++) {

                    let country = obj[i]['country'].toString();           // COUNTRY OF METAL
                    let mm = obj[i]['mm'].toString();                     // THICKNESS OF METAL
                    let countryMm = country + '_' + mm;                   // CONCATENATION OF country & mm
                    let texture = obj[i]['texture'];
                    let items = {
                        profil: parseInt(obj[i]['profilh10']),            // PRICE OF gsx$profilh10shiferh18 (mame of cell in GSheet db)
                        trapec: parseInt(obj[i]['trapech18']),            // PRICE OF gsx$trapech18 (mame of cell in GSheet db)
                        mch_trevizo: parseInt(obj[i]['mchtrevizo']),      // PRICE OF gsx$mch (mame of cell in GSheet db)
                        mch_monterrey: parseInt(obj[i]['mchmonterrey']),  // PRICE OF gsx$mch (mame of cell in GSheet db)
                        complect: parseInt(obj[i]['complect'])            // PRICE OF gsx$complect (mame of cell in GSheet db)
                    };

                    // PART FOR DEL ITEM WITHOUT RICE
                    for (let item in items) {
                        if (isNaN(items[item])) {
                            delete items[item]
                        }
                    }
                    // END OF PART


                    for (let name in obj[i]) {
                        if (/\bral\d{4}\b/.test(name)) {

                            if (obj[i][name] == '1') {

                                prod[texture][name][countryMm] = items //  PUSH OBJECT WITH ITEMS PRICE TO METAL CATEGORY

                            }// IF HAVE THIS RAL ?

                        } // FIND ALL RALS

                        if (texture === 'zinc') {
                            prod[texture][countryMm] = items
                        }

                    } // END OF FOR...IN FOR KEY FROM MAIN OBJECT

                } // END OF LOOP FOR PRODUCT
                console.log(prod)


            } // END OF FUNCTION

        }
    }
</script>

